header{
    width: 100%;
    height: 100vh;
    position: relative; 
    background-image: url("./img/fondo1.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}
.iconos{
    font-size: 40px;
}
.iconos a{
    text-decoration: none;
    color: white;
    padding: 5px;
    margin: 0;
}
.iconos a:hover{
    color: #212529;
}
.title{
    color: white;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);  
}
.btn{
    background-color: #5080e5;
    border-radius: 20px;
    color: white;
    font-weight: bold;
    padding-left: 20px;
    padding-right: 20px;
}
.btn:hover{
    background: #212529;
    color: white;
}
.nombre{
    font-size: 50px;
}
.profesion{
   font-size: 40px;
}
.descrip{
    font-size: 25px;
    font-weight: bold;
}